@import 'utils';
@import 'fonts';
@import 'typography';

body {
  margin: 0;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.wall {
  background-color: #f9b47d;
  background-image: url(../images/brick-wall.svg);
  border: #e1e1e1 solid 10px;
  width: 400px;
  height: 400px;
  position: relative;
  cursor: pointer;
}

.spider {
  position: absolute;
  top: 0;
  left: 0;
  width: 50px;
  height: 50px;
  transition: 1s;
}
